.App {
  display: flex;
  justify-content: space-between;
}

.App_Form {
  display: flex;
  background-color: #F5F5F5;
  color: #1F2B56;
  width: 402px;
  border-radius: 60px;
  margin: 1rem auto;
}

@media screen and (max-width: 950px) and (min-width: 200px) {
  .App {
    display: flex;
    flex-direction: column;
  }
  .App_Form {
    width: 360px;
    margin: 1rem auto;
  }
}

